
* {
  padding: 0;
  margin: 0;
}
body {
  height: 200vh;
}
.container1 {
width: 380px;
height: 158px;
left: 0px;
top: 0px;

background: linear-gradient(94.23deg, #24CE9E 47.9%, rgba(36, 206, 158, 0) 148.7%);
border-radius: 0px 0px 20px 20px;
z-index: -1;
}

.container1 .text {
position: absolute;
width: 105px;
height: 24px;
left: 23px;
top: 64px;

font-family: 'Inter';
font-style: normal;
font-weight: 800;
font-size: 20px;
line-height: 24px;

color: #FFFFFF;
}

.container1 p {
position:absolute;
width: 183px;
height: 15px;
left: .2px;

font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 15px;

color: #FFFFFF;

}

.search {
position: absolute;
width: 280px;
height: 45px;
left: 35px;
top: 127px;

background: #FFFFFF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
border-radius: 15px;
 
}

.search input {
  position: relative;
  width: 220px;
  height: 45px;
  border: none;
  outline: none;
}

 ::placeholder {
  position: relative;
  left: 12px;
}

.search [type='text'] {
  padding-left: 33px;
}

.text1 p{
  position: relative;
  top: 42px;
  left: 22px;
  color: black;
  font-size: large;
  font-weight: 800;
}
.container2 {
  width:1vw;
  position: relative;
  top: 83px;
  left: 45px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 32px;
}

.container2 img {
  width: 133px;
  border-radius: 22px;
}



.container2 .box5 {
  position: relative;
  left: 33vw;
  grid-column-end: 2;
}

.discount {
  display: flex;
  align-items: center;
position: relative;
width: 380px;
height: 78px;
left: 0px;
top: 31px;
color: white;
font-weight: 700;
background: #24CE9E;

}

.discount p {
  position: relative;
  left: 22px;
  width: 166px;
}

.discount button {
  position: relative;
width: 94px;
height: 31px;
left: 84px;

background: #FFFFFF;
border-radius: 15px;

}

.text2 p {
  position: relative;
  color: black;
  top: 92px;
  left: 22px;
  font-weight: 800;
}

.container3 {
  width:1vw;
  position: relative;
  top: 143px;
  left: 45px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 32px;
  row-gap: 66px;
}

.container3 img {
  width: 133px;
  border-radius: 12px;
  box-shadow: 6px 2px 11px black;
}


.content {
  opacity: 0;
  transform: translateY(80px); /* For slide-up effect */
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

.content.visible {
  opacity: 1;
  transform: translateY(0);
}